function PivotTable (pivotStrategy, logger) {
  this.pivotStrategy = pivotStrategy
  this.rows = {}
  this._sortedRows = null
  this._sortedColumns = null
  this.totals = {}
  this.sum = 0
  this.num = 0
  this.detailsNum = 0
  if (this.pivotStrategy.getColumnsFunction) {
    this.totals = this.pivotStrategy.getColumnsFunction()
  }
  this.logger = logger
}

PivotTable.prototype.sortedColumns = function () {
  if (this._sortedColumns == null) {
    this._sortedColumns = TimesheetUtils.objectValues(this.totals)
    if (this.pivotStrategy.columnKeyClass && this.pivotStrategy.columnKeyClass.hasOwnProperty('comparator')) {
      TimesheetUtils.sortByProperty(this._sortedColumns, 'columnKey', false, this.pivotStrategy.columnKeyClass.comparator)
    }
  }
  return this._sortedColumns
}

PivotTable.prototype.sortedRows = function () {
  if (this._sortedRows == null) {
    // var performance = TimesheetUtils.getPerformance();
    // var t1 = performance.now();
    var keys = Object.keys(this.rows)
    if (this.pivotStrategy.options.orderByField && this.pivotStrategy.rowKeyClass.hasOwnProperty('getRowComparator')) {
      var values = TimesheetUtils.objectValues(this.rows)
      values.sort(this.pivotStrategy.rowKeyClass.getRowComparator(this.pivotStrategy.options.orderByField))
      keys = values.map(function (row) {
        return row.rowKey.keyValue
      })
    } else if (this.pivotStrategy.rowKeyClass.hasOwnProperty('comparator')) {
      keys.sort(this.pivotStrategy.rowKeyClass.comparator)
    }
    // var t2 = performance.now();
    this.fieldTotals = {}
    this._sortedRows = keys.map(function (key) {
      var row = this.rows[key]
      if (this.pivotStrategy.rowKeyClass.hasOwnProperty('dataComparator')) {
        row.data.sort(this.pivotStrategy.rowKeyClass.dataComparator)
      }
      if (typeof row.rowKey === 'object' && !row.categoryRow) {
        var issue = row.rowKey._issue || row.rowKey.issue || row.rowKey.field
        for (var issueField in issue.fields) {
          var field = issue.fields[issueField] // sum all issue fields
          if (field && typeof field === 'number') {
            this.fieldTotals[issueField] = this.fieldTotals[issueField] || 0 // init
            this.fieldTotals[issueField] += field // add
          }
        }
      } // else PivotTableTest#22
      return row
    }, this)
    // var t3 = performance.now();
    // keys.length > 100 && this.logger && this.logger.debug('sortedRows performance', {_: this.logger._  , rows: keys.length, time: t3 - t1, t1: t2 - t1, t2: t3 - t2});
    if (!this.pivotStrategy.options.showWeekends) {
      for (var key in this.totals) {
        if (this.totals[key].columnKey.isWeekend && !this.totals[key].sum) {
          delete this.totals[key]
          for (var i = 0; i < this._sortedRows.length; i++) {
            delete this._sortedRows[i].columns[key]
          }
          for (var i = 0; i < this._sortedColumns.length; i++) {
            if (this._sortedColumns[i].columnKey.keyValue == key) { // eslint-disable-line eqeqeq
              this._sortedColumns.splice(i, 1)
            }
          }
        }
      }
    }
  }
  return this._sortedRows
}

PivotTable.prototype.addEntry = function (pivotEntry) {
  var pivotRowType = this.pivotStrategy.pivotRowType || PivotRow
  var pivotTable = this
  var pivotRow = TimesheetUtils.getOrCreate(this.rows, pivotEntry.rowKey, pivotRowType, function () {
    if (pivotTable.pivotStrategy.getColumnsFunction) {
      this.columns = pivotTable.pivotStrategy.getColumnsFunction(pivotEntry.worklog && new Date(pivotEntry.worklog.started))
    }
    this.moreFields = pivotTable.moreFields
  })
  pivotRow.add(pivotEntry)
  for (var i = 0; i < pivotEntry.columnKeys.length; i++) {
    var columnKey = pivotEntry.columnKeys[i]
    var value = pivotEntry.values[columnKey.keyValue]
    var total = TimesheetUtils.getOrCreate(this.totals, columnKey, PivotColumn, function () {
      this.isTotalColumn = true
    })
    total.add(pivotEntry, value)
  }
  if (typeof pivotEntry.value === 'number') {
    this.sum += pivotEntry.value
  }
  this._sortedRows = null // reset
}

PivotTable.prototype.add = function (issue) {
  var pivotEntries = this.pivotStrategy.getEntries(issue)
  if (pivotEntries.length > 0) {
    this.num++
  }
  for (var i in pivotEntries) {
    var pivotEntry = pivotEntries[i]
    this.addEntry(pivotEntry)
    this.detailsNum += pivotEntry.columnKeys.length
  }
  return pivotEntries
}

PivotTable.prototype.postProcessPivotTable = function (options) {
  var pivotTable = this
  var addEmptyRows = function () {
    if (options.pivotTableType === 'Calendar') {
      TimesheetUtils.getWeeksForMonth(options).map(function (row) {
        TimesheetUtils.getOrCreate(pivotTable.rows, {
          keyValue: moment(row.date).format('YYYYMMDD'),
          issue: {},
          field: { startDate: row.date, weekNumber: TimesheetUtils.getNormalizedWeekNumber(row.date) }
        }, PivotRow, function () {
          this.columns = pivotTable.pivotStrategy.getColumnsFunction(row.date)
        })
      })
    }
  }
  var highlightHours = function () {
    var setColor = function (obj, threshold) {
      if (obj.sum > threshold) {
        obj.color = options.configOptions.workOverLimitColor
        obj.title = '+' + DrawHelper.getFormattedTime(obj.sum - threshold, options.jiraConfig)
      } else if (obj.sum < threshold) {
        obj.color = options.configOptions.workUnderLimitColor
        obj.title = '-' + DrawHelper.getFormattedTime(threshold - obj.sum, options.jiraConfig)
      }
    }

    if (options.pivotTableType === 'Timesheet' &&
      (options.user || (options.groupByField === 'workeduser' && !options.categorizeByField)) &&
      (options.configOptions.workUnderLimitColor || options.configOptions.workOverLimitColor)) {
      var workingSecondsPerDay = options.jiraConfig.workingHoursPerDay * 60 * 60
      var rows = Object.keys(pivotTable.rows)
      var totals = Object.keys(pivotTable.totals)
      var workingDaysTotal = 0
      var workedUsers = options.groupByField === 'workeduser' ? rows.length : 1
      for (var totalKey in totals) {
        var total = pivotTable.totals[totals[totalKey]]
        workingDaysTotal += total.columnKey.workingDays
        setColor(total, workingSecondsPerDay * total.columnKey.workingDays * workedUsers)
      }
      if (options.groupByField === 'workeduser') {
        for (var rowKey in rows) {
          var row = pivotTable.rows[rows[rowKey]]
          var columns = Object.keys(row.columns)
          setColor(row, workingSecondsPerDay * workingDaysTotal)
          for (var columnKey in columns) {
            var column = row.columns[columns[columnKey]]
            setColor(column, workingSecondsPerDay * column.columnKey.workingDays)
          }
        }
      }
      setColor(pivotTable, workingSecondsPerDay * workingDaysTotal * workedUsers)
    }
  }
  var addCategoryRow = function () {
    if (options.categorizeByField && options.configOptions.showCategoryRow && options.pivotTableType !== 'PieChart') {
      var rows = Object.keys(pivotTable.rows)
      for (var rowKey in rows) {
        var row = pivotTable.rows[rows[rowKey]]
        var category = row.rowKey._project
        var decategorizedRow = pivotTable.rows[category]
        if (decategorizedRow) {
          decategorizedRow.detailsNum += row.detailsNum
          decategorizedRow.sum += row.sum
          Array.prototype.push.apply(decategorizedRow.data, row.data)
          for (var i in row.columns) {
            if (decategorizedRow.columns[i]) {
              decategorizedRow.columns[i].detailsNum += row.columns[i].detailsNum
              decategorizedRow.columns[i].sum += row.columns[i].sum
              Array.prototype.push.apply(decategorizedRow.columns[i].entries, row.columns[i].entries)
            } else {
              decategorizedRow.columns[i] = TimesheetUtils.clone(row.columns[i])
            }
          }
          if (decategorizedRow.rowKey._issue) {
            var issueFields = decategorizedRow.rowKey._issue.fields
            for (var issueField in row.rowKey._issue.fields) { // iterate over original fields
              var field = row.rowKey._issue.fields[issueField]
              if (field && typeof field === 'number') {
                issueFields[issueField] = issueFields[issueField] || 0 // init
                issueFields[issueField] += field // add
              }
            }
          }
          for (let i = 0; i < row._issueKeys.length; i++) {
            if (decategorizedRow._issueKeys.indexOf(row._issueKeys[i]) < 0) {
              decategorizedRow.num++
              decategorizedRow._issueKeys.push(row._issueKeys[i])
            }
          }
        } else {
          var newRow = TimesheetUtils.clone(row)
          newRow.categoryRow = true
          newRow.rowKey.keyValue = category
          newRow.rowKey.keyName = 'categoryrow'
          newRow.rowKey.field = { name: '' }
          newRow.rowKey._field = ''
          pivotTable.rows[category] = newRow
        }
      }
    }
  }
  var calculateAveragesInRow = function (columns, row, isTotalRow) {
    var columnNumber = 0
    for (var columnKey in columns) {
      var column = columns[columnKey]
      const unique = !options.groupByField && !isTotalRow ? column.detailsNum
        : column.entries.map(entry => options.groupByField && !row.categoryRow && !isTotalRow
          ? (entry.worklog && entry.worklog.issueId) || (entry.rowKey._issue && entry.rowKey._issue.id)
          : entry.rowKey.keyValue)
          .reduce((acc, entry) => {
            if (acc.indexOf(entry) < 0) {
              acc.push(entry)
            }
            return acc
          }, []).length
      column.avg = (column.sum || 0) / (unique || 1)
      if (column.sum) columnNumber++
    }
    row.avg = (row.sum || 0) / (columnNumber || 1)
  }
  var calculateAverages = function () {
    if (options.configOptions.showDetailsCount) {
      for (var row in pivotTable.rows) {
        calculateAveragesInRow(pivotTable.rows[row].columns, pivotTable.rows[row])
      }
      calculateAveragesInRow(pivotTable.totals, pivotTable, 'totalRow')
    }
  }
  var calculateHourlyRateInColumn = column => {
    if (column['62rate']) {
      column['62rate'].sum = column['3timespent'].sum
        ? column['7cost'].sum / (column['3timespent'].sum / 3600)
        : 0
    }
  }
  var calculateHourlyRates = () => {
    if (options.pivotTableType === 'CostReport') {
      for (var row in pivotTable.rows) {
        calculateHourlyRateInColumn(pivotTable.rows[row].columns)
      }
      calculateHourlyRateInColumn(pivotTable.totals)
    }
  }

  addEmptyRows()
  highlightHours()
  addCategoryRow()
  calculateAverages()
  calculateHourlyRates()
}

var PivotTableFactory = {} // very basic singleton
PivotTableFactory.createPivotTable = function (options, logger) {
  var pivotStrategy
  var groupByField = options.groupByField
  var createStrategy = function (strategyClass, groupByField, columnKeyClass, options) {
    if (groupByField) {
      return new strategyClass(PivotKey.GroupByField, columnKeyClass, options)
    } else {
      return new strategyClass(PivotKey.Issue, columnKeyClass, options)
    }
  }
  switch (PivotTableType[options.pivotTableType]) {
    case PivotTableType.PieChart:
    case PivotTableType.IssueWorkedTimeByUser:
      if (PivotTableType.PieChart === PivotTableType[options.pivotTableType] && !options.orderByField) {
        options.orderByField = TimesheetUtils.FIELDS.WORKED.id
      }
      pivotStrategy = createStrategy(WorkedTimePivotStrategy, groupByField, PivotKey.User, options)
      break
    case PivotTableType.IssueWorkedTimeByStatus:
      pivotStrategy = createStrategy(WorkedTimePivotStrategy, groupByField, PivotKey.Status, options)
      break
    case PivotTableType.IssueWorkedTimeByLabel:
      pivotStrategy = createStrategy(LabelPivotStrategy, groupByField, PivotKey.Generic, options)
      break
    case PivotTableType.IssuePassedTimeByStatus:
      pivotStrategy = createStrategy(PassedTimePivotStrategy, groupByField, PivotKey.Status, options)
      break
    case PivotTableType.IssuePassedTimeByResolution:
      pivotStrategy = createStrategy(ResolutionTimePivotStrategy, groupByField, PivotKey.Status, options)
      break
    case PivotTableType.TimeTracking:
      pivotStrategy = createStrategy(TimeTrackingStrategy, groupByField, PivotKey.Generic, options)
      break
    case PivotTableType.Timesheet:
      var monthView = options.monthView
      var defaultKeyFunction = monthView ? PivotKey.WeekOfTheYear : PivotKey.DayOfTheWeek
      var keyFunction = this.createPivotKey(options, defaultKeyFunction)
      pivotStrategy = createStrategy(TimesheetStrategy, groupByField, keyFunction, options)
      break
    case PivotTableType.Timespent:
      pivotStrategy = createStrategy(TimespentStrategy, groupByField, PivotKey.Generic, options)
      break
    case PivotTableType.CostReport:
      pivotStrategy = createStrategy(CostStrategy, groupByField, PivotKey.Generic, options)
      break
    case PivotTableType.TimeEntry:
      pivotStrategy = new TimeEntryStrategy(PivotKey.Issue, options)
      break
    case PivotTableType.TimeBalance:
      pivotStrategy = createStrategy(TimeBalanceStrategy, groupByField, PivotKey.Generic, options)
      break
    case PivotTableType.Calendar:
      pivotStrategy = createStrategy(CalendarStrategy, 'workedweek', PivotKey.Generic, options)
      break
    default:
      if (options.pivotTableType === 'IssueTime') {
        pivotStrategy = new IssueTimeStrategy(PivotKey.Issue, options)
      } else if (options.pivotTableType === 'IssueTimeBalance') {
        pivotStrategy = new TimeBalanceStrategy(PivotKey.Issue, PivotKey.Generic, options)
      } else {
        throw new Error('Unknown PivotTableType ' + options.pivotTableType)
      }
  }
  var pivotTable = new PivotTable(pivotStrategy, logger)
  pivotTable.moreFields = options.moreFields
  return pivotTable
}

PivotTableFactory.createPivotKey = function (options, defaultKeyFunc) {
  var result = defaultKeyFunc
  if (options != null) {
    switch (options.sum) {
      case 'month':
        result = PivotKey.MonthOfTheYear
        break
      case 'week':
        result = PivotKey.WeekOfTheYear
        break
      case 'day':
        result = PivotKey.DayOfTheWeek
        break
    }
  }
  return result
}

if (typeof module === 'object' && module.exports) {
  module.exports = PivotTableFactory
  var PivotKey = require('./PivotKey')
  var PivotRow = require('./PivotRow')
  var PivotColumn = require('./PivotColumn')
  var TimesheetStrategy = require('./TimesheetStrategy')
  var PivotStrategy = require('./PivotStrategy')
  var PivotTableType = require('./PivotTableType')
  var WorkedTimePivotStrategy = PivotStrategy.WorkedTimePivotStrategy
  var TimeEntryStrategy = PivotStrategy.TimeEntryStrategy
  var IssueTimeStrategy = PivotStrategy.IssueTimeStrategy
  var PassedTimePivotStrategy = PivotStrategy.PassedTimePivotStrategy
  var ResolutionTimePivotStrategy = PivotStrategy.ResolutionTimePivotStrategy
  var TimeTrackingStrategy = PivotStrategy.TimeTrackingStrategy
  var TimeBalanceStrategy = PivotStrategy.TimeBalanceStrategy
  var TimespentStrategy = PivotStrategy.TimespentStrategy
  var LabelPivotStrategy = PivotStrategy.LabelPivotStrategy
  var CostStrategy = PivotStrategy.CostStrategy
  var CalendarStrategy = PivotStrategy.CalendarStrategy

  var TimesheetUtils = require('../../timesheetUtils')
}
