// This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with
// each doing the same thing just structuring the functions/data differently.
app.service('trackingService', ['$q', 'applicationLoggingService', 'configurationService', 'dataService', 'loggedInUser',
  function ($q, logger, configurationService, dataService, $loggedInUser) {
    var trackingService = this

    var $initialized = $q.defer()
    $q.all([dataService.getAppProperties(), dataService.getServerInfo(), $loggedInUser]).then(values => {
      const appProperties = values[0]
      const serverInfo = values[1]
      const ua = new UAParser(navigator.userAgent)
      const tenantName = serverInfo.baseUrl.match(/https:\/\/(.+?)\./)[1]
      Avo.initAvo(
        {
          env: ['timereports', 'avelytchenko'].indexOf(tenantName) >= 0 ? 'dev' : 'prod',
          webDebugger: false,
          strict: false
        },
        {
          appVersion: appProperties.version || '1',
          instanceUuid: serverInfo.scmInfo,
          baseUrl: serverInfo.baseUrl,
          jiraSen: (appProperties.license && appProperties.license.supportEntitlementNumber) || 'none',
          webBrowser: ua.getBrowser().name || 'unknown',
          webBrowserVersion: ua.getBrowser().version || 'unknown',
          operatingSystem: ua.getOS().name || 'unknown',
          operatingSystemVersion: ua.getOS().version || 'unknown'
        }
      )
      var user = values[2]
      configurationService.getUserConfiguration(user).then(userPreferences => {
        track('identifiedUser', {
          userId_: user.accountId,
          id: user.accountId,
          userTimezone: moment.tz.guess(),
          userContinent: (moment.tz.guess() || '').split('/')[0],
          userLocale: moment.locale(),
          originProduct: window.appName,
          jiraAdministrator: user.isAdministrator,
          tempoToken: !!userPreferences.tempoToken.val,
          weekendType: userPreferences.weekendType.val,
          reportingDayDefaultValue: userPreferences.reportingDay.val,
          durationTypeDefaultValue: userPreferences.durationType.val,
          maximumFractionDigits: userPreferences.maxFractionDigits.val,
          durationTypeForExport: userPreferences.durationTypeForExport.val,
          startedTimeInStatus: userPreferences.startedTimeInStatus.val,
          inProgressIssuesJql: userPreferences.inProgressIssuesJql.val,
          showDetailsCount: userPreferences.showDetailsCount.val,
          showCategoryRow: userPreferences.showCategoryRow.val,
          showTotalInFront: userPreferences.showTotalInFront.val,
          workingTimeInStatusFrom: userPreferences.workingTimeInStatus.val && userPreferences.workingTimeInStatus.val.from,
          workingTimeInStatusTo: userPreferences.workingTimeInStatus.val && userPreferences.workingTimeInStatus.val.to
        })
        $initialized.resolve(trackingService)
      })
    })

    var track = (event, data) => {
      if (Avo[event]) {
        try {
          Avo[event](data)
        } catch (err) {
          logger.error('Avo Error', err)
        }
      } else {
        logger.debug('Unsupported event', event)
      }
    }

    this.trackLoggedTime = (worklog, $scope, action) => {
      track('loggedTime', {
        originPage: worklog.standaloneDialog
          ? 'Standalone Worklog Dialog'
          : ['IssueTime', 'IssueTimeBalance'].indexOf($scope.pivotTableType) >= 0
            ? 'Jira Issue View'
            : $scope.isGadget
              ? 'Gadget'
              : 'Report Page',
        originEcosystem: 'Jira',
        originAction: worklog.action || action,
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        startTime: TimesheetUtils.formatDate(moment(worklog.started || worklog.date).toDate()),
        descriptionLength: worklog.comment ? worklog.comment.length : 0,
        hoursWorked: moment.duration(worklog.timeSpentSeconds || TimesheetUtils.getTimeSpentMs(worklog.timeSpent || 0, $scope.jiraConfig) / 1000, 'seconds').asHours(),
        measuredTimeInForm: worklog.now ? moment.duration(TimesheetUtils.getPerformance().now() - worklog.now, 'milliseconds').asSeconds() : 0,
        measuredTimeInIssuePicker: 0,
        preSelectedTask: worklog.preSelectedIssue || false,
        remainingEstimateHours: moment.duration(TimesheetUtils.getTimeSpentMs(worklog.estimateValue || 0, $scope.jiraConfig), 'milliseconds').asHours(),
        currentRemainingEstimateUsed: (worklog.estimateType || 'auto') === 'auto',
        expandedForm: worklog.expandedForm == null ? true : worklog.expandedForm,
        requireWorkDescription: !!$scope.workDescriptionRequired, // not null
        originalTimeSpent: worklog.timeSpent,
        loggedWorkForOther: !!worklog.user // bool
      })
    }

    this.trackEditedTimeRecord = (worklog, $scope) => {
      if (!worklog.storedData) {
        return
      }
      var attribute = []
      if (!TimesheetUtils.sameDay(moment(worklog.started).toDate(), moment(worklog.storedData.started).toDate())) {
        attribute.push('Date')
      }
      if (!TimesheetUtils.sameTime(moment(worklog.started).toDate(), moment(worklog.storedData.started).toDate())) {
        attribute.push('Start Time')
      }
      if (worklog.timeSpentSeconds !== worklog.storedData.timeSpentSeconds) {
        attribute.push('Hours Worked')
      }
      if (worklog.comment !== worklog.storedData.comment) {
        attribute.push('Description')
      }
      if (worklog.estimateValue) {
        attribute.push('Remaining Estimate')
      }
      track('editedTimeRecord', {
        originPage: ['IssueTime', 'IssueTimeBalance'].indexOf($scope.pivotTableType) >= 0
          ? 'Jira Issue View'
          : $scope.isGadget
            ? 'Gadget'
            : 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        startTime: TimesheetUtils.formatDate(moment(worklog.started || worklog.date).toDate()),
        descriptionLength: worklog.comment ? worklog.comment.length : 0,
        hoursWorked: moment.duration(worklog.timeSpentSeconds || TimesheetUtils.getTimeSpentMs(worklog.timeSpent || 0, $scope.jiraConfig) / 1000, 'seconds').asHours(),
        measuredTimeInForm: worklog.now ? moment.duration(TimesheetUtils.getPerformance().now() - worklog.now, 'milliseconds').asSeconds() : 0,
        measuredTimeInIssuePicker: 0,
        attribute: attribute,
        dateOfTimeRecord: TimesheetUtils.formatDateForSearch(moment().toDate())
      })
    }

    this.trackDeletedTimeRecord = (worklog, $scope) => {
      track('deletedTimeRecord', {
        originPage: ['IssueTime', 'IssueTimeBalance'].indexOf($scope.pivotTableType) >= 0
          ? 'Jira Issue View'
          : $scope.isGadget
            ? 'Gadget'
            : 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        startTime: TimesheetUtils.formatDate(moment(worklog.started || worklog.date).toDate()),
        descriptionLength: worklog.comment ? worklog.comment.length : 0,
        hoursWorked: moment.duration(worklog.timeSpentSeconds || TimesheetUtils.getTimeSpentMs(worklog.timeSpent || 0, $scope.jiraConfig) / 1000, 'seconds').asHours(),
        dateOfTimeRecord: TimesheetUtils.formatDateForSearch(moment().toDate())
      })
    }

    this.trackViewedReport = ($scope, routeParams) => {
      track('viewedReport', {
        originPage: $scope.isGadget ? 'Gadget' : 'Report Page',
        originEcosystem: 'Jira',
        originAction: $scope.loaded ? 'Automatic Update' : 'Clicked on View Report',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        periodLength: $scope.startDate.value && $scope.endDate.value && Math.round(moment.duration($scope.endDate.value - $scope.startDate.value, 'milliseconds').asDays()),
        reportingDay: $scope.reportingDay,
        orderBy: routeParams.orderByField || '',
        groupBy: routeParams.groupByField || '',
        categorizeBy: routeParams.categorizeByField || '',
        showWeekends: $scope.showWeekends.enabled,
        sumSubtasks: $scope.sumSubTasks.enabled,
        additionalFields: '' + (routeParams.moreFields || ''),
        allUsers: !!($scope.allUsers && $scope.allUsers.enabled),
        filterOrProject: '' + (routeParams.filterOrProjectId || ''),
        childrenFilter: '' + (routeParams.childrenFilterId || '')
      })
    }

    this.trackSubscribedToView = ($scope, job) => {
      track('subscribedToView', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originAction: 'Clicked on Subscribe',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        subscriptionPeriod: ['Daily', 'Every Monday', 'Every Tuesday', 'Every Wednesday', 'Every Thursday',
          'Every Friday', 'Every Saturday', 'Every Sunday'][job.on + 1],
        time: job.at,
        sentIfTimesheetIsNotComplete: job.ifIncomplete,
        exportAttached: !!job.addCsv // not null
      })
    }

    this.trackShareView = (forYourself) => {
      track('sharedView', { forYourself })
    }

    this.trackStartedTracker = ($scope, worklog) => {
      track('startedTracker', {
        originPage: $scope.pivotTableType === 'IssueTime' ? 'Jira Issue View' : $scope.isGadget ? 'Gadget' : 'Report Page',
        originEcosystem: 'Jira',
        originAction: 'Clicked in Cell',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        minutesOnTracker: worklog.timeSpentSeconds / 60,
        descriptionLength: worklog.comment ? worklog.comment.length : 0
      })
    }

    this.trackPausedTracker = ($scope, worklog) => {
      track('pausedTracker', {
        originPage: $scope.pivotTableType === 'IssueTime' ? 'Jira Issue View' : $scope.isGadget ? 'Gadget' : 'Report Page',
        originEcosystem: 'Jira',
        originAction: 'Clicked in Cell',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        minutesOnTracker: worklog.timeSpentSeconds / 60,
        descriptionLength: worklog.comment ? worklog.comment.length : 0
      })
    }

    this.trackDeletedTracker = ($scope, worklog) => {
      track('deletedTracker', {
        originPage: $scope.pivotTableType === 'IssueTime' ? 'Jira Issue View' : $scope.isGadget ? 'Gadget' : 'Report Page',
        originEcosystem: 'Jira',
        originAction: 'Clicked in Cell',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        minutesOnTracker: worklog.timeSpentSeconds / 60,
        descriptionLength: worklog.comment ? worklog.comment.length : 0
      })
    }

    this.trackModifiedGadget = () => {
      track('modifiedGadget')
    }

    this.trackAppliedGrouping = ($scope, customFieldName) => {
      track('appliedGrouping', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        customFieldName
      })
    }

    this.trackRemovedGrouping = ($scope, customFieldName) => {
      track('removedGrouping', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        customFieldName
      })
    }

    this.trackAddedCategorization = ($scope, customFieldName) => {
      track('addedCategorization', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        customFieldName
      })
    }

    this.trackRemovedCategorization = ($scope, customFieldName) => {
      track('removedCategorization', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        customFieldName
      })
    }

    this.trackExportedData = ($scope, exportType) => {
      track('exportedData', {
        originPage: $scope.pivotTableType === 'IssueTime' ? 'Jira Issue View' : $scope.isGadget ? 'Gadget' : 'Report Page',
        originEcosystem: 'Jira',
        originAction: 'Clicked on Export Button',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        periodLength: $scope.startDate.value && $scope.endDate.value && Math.round(moment.duration($scope.endDate.value - $scope.startDate.value, 'milliseconds').asDays()),
        exportType
      })
    }

    this.trackAddedColumn = ($scope, customFieldName) => {
      track('addedColumn', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        columnType: 'Custom Field',
        customFieldName
      })
    }

    this.trackRemovedColumn = ($scope, customFieldName) => {
      track('removedColumn', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        columnType: 'Custom Field',
        customFieldName
      })
    }

    this.trackAppliedFilter = ($scope) => {
      track('appliedFilter', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType
      })
    }

    this.trackModifiedDisplay = ($scope, data) => {
      track('modifiedDisplay', {
        originPage: 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView: $scope.pivotTableType,
        display: data.display,
        switchedView: data.switchedView || null,
        summedViewBy: data.summedViewBy || null
      })
    }

    this.trackNavigated = ($scope, pageFrom, pageTo, originView, selectedView) => {
      track('navigated', {
        originPage: $scope.pivotTableType === 'IssueTime' ? 'Jira Issue View' : $scope.isGadget ? 'Gadget' : 'Report Page',
        originEcosystem: 'Jira',
        originProduct: window.appName,
        originView,
        segmentPageName_: pageFrom,
        pageFrom,
        pageTo,
        selectedView,
        onboardingMessage: false
      })
    }
    return $initialized.promise
  }
])
